import React, { useEffect, useState } from 'react'

import { 
  CCard,
  CCardBody,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react'
import WidgetsDropdown from '../widgets/WidgetsDropdown'

const Dashboard = () => {
  const [enquiries, setEnquiries] = useState([]);

  useEffect(() => {

    var requestOptions = { 
      method: 'POST',
      redirect: 'follow'
    };

    let url =  process.env.REACT_APP_API_URL+"enquire/list"
    fetch(url, requestOptions)
      .then(response => response.json())
      .then(result => { setEnquiries(result.data) })
      .catch(error => console.log('error', error));

  }, []);

  return (
    <>
      <WidgetsDropdown />
      
      <CCard className="mb-4">
        <CCardBody>
          <h4 id="traffic" className="card-title mb-4" style={{fontWeight:700}}>
            Latest Enquiries
          </h4>
          <CTable bordered striped>
                  <CTableHead>
                    <CTableRow>      
                      <CTableHeaderCell>
                        Enquire Id
                      </CTableHeaderCell>                
                      <CTableHeaderCell width={150}>
                        Name
                      </CTableHeaderCell>                     
                      <CTableHeaderCell>
                        Email
                      </CTableHeaderCell>
                      <CTableHeaderCell>
                        Phone 
                      </CTableHeaderCell> 
                      
                      <CTableHeaderCell width={100}>
                        Subject
                      </CTableHeaderCell>
                      <CTableHeaderCell width={200}>
                        Message
                      </CTableHeaderCell>
                    </CTableRow>
                  </CTableHead>
                <CTableBody>
                  {enquiries.map((i, key) => (
                  <CTableRow key={key}>
                    <CTableDataCell>
                      {i.enquire_id}
                    </CTableDataCell>
                    <CTableDataCell>
                      {i.name}
                    </CTableDataCell>
                    <CTableDataCell>
                      {i.email}
                    </CTableDataCell>   
                    <CTableDataCell>
                      {i.phone}
                    </CTableDataCell>               
                    <CTableDataCell>
                      {i.subject}
                    </CTableDataCell>
                    <CTableDataCell>
                      {i.message}
                    </CTableDataCell>
                  </CTableRow>
                ))}
                </CTableBody>
                </CTable>

          
         
        </CCardBody>
      </CCard>

    </>
  )
}

export default Dashboard
